html {
  box-sizing: border-box;
}

.todo-list {
  list-style: none;
}

.todo-item {
  margin-bottom: 5px;
}

.todo-button {
  background-color: #87ceeb;
  border-color: #8a2be2;
  border-radius: 25px;
  margin-left: 10px;
}

.done {
  text-decoration: line-through;
}

form {
  flex-direction: column;
  width: 500px;
  margin-top: 10px;
  margin-bottom: 30px;
  display: flex;
}

.submit-btn {
  background-color: #daa520;
  width: 508px;
}

.hide {
  display: none;
}

.close-btn {
  border-color: #87ceeb;
  border-radius: 50px;
  position: absolute;
  top: 10px;
  right: 10px;
}

.backdrop {
  background-color: #0909094d;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

.modal {
  background-color: #fff;
  width: 350px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.modal-form {
  margin-left: auto;
  margin-right: auto;
  padding: 45px 20px 20px;
}

input {
  width: 500px;
  margin-bottom: 11px;
}

.btn {
  width: 100%;
  height: 30px;
}

